import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpModule } from '@angular/http';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import {TreeModule, TreeNode} from 'primeng/primeng';
import {AutoCompleteModule} from 'primeng/primeng';
import {TreeDragDropService} from 'primeng/primeng';
import { SharedModule } from '../shared/shared.module';
import { CreateAccountComponents } from './create.account.component';
import { ModifyAccountComponent } from './modify.account.component';

import { AuthGuard } from '../guards/auth.guard';
import { SiteLayoutComponent } from '../layout/site-layout.component';

@NgModule({
  imports: [

      BrowserModule,
      HttpModule,
      CommonModule,
      FormsModule,
      TreeModule,
      AutoCompleteModule,
      SharedModule,
      RouterModule.forRoot([
          {
              path: 'accounts',
              component: SiteLayoutComponent,
              children: [
                  { path: 'create-account', component: CreateAccountComponents, canActivate: [AuthGuard]},
                  { path: 'modify-account', component: ModifyAccountComponent, canActivate: [AuthGuard] }
              ]
          }                                 
      ])

  ],
  declarations: [
      CreateAccountComponents,
      ModifyAccountComponent
  ],
  providers: [
    
  ]
})
export class AccountsModule {}



