import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpModule } from '@angular/http';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';

import { DeactivateCardComponent } from './deactivate.card.component';
import { ChangeTariffPlanComponent } from './change.tariff.plan.component';
import { PasswordModule } from 'primeng/primeng';

import { AuthGuard } from '../guards/auth.guard';
import { SiteLayoutComponent } from '../layout/site-layout.component';


@NgModule({
  imports: [

      BrowserModule,
      HttpModule,
      CommonModule,
      FormsModule,
      PasswordModule,
      RouterModule.forRoot([
          {
          path: 'actions',
          component: SiteLayoutComponent,
          children: [
              { path: 'change-tariff-plans', component: ChangeTariffPlanComponent, canActivate: [AuthGuard] },
              { path: 'deactivate-cards', component: DeactivateCardComponent, canActivate: [AuthGuard] }

          ]

          }
                  
    ])
  ],
  declarations: [
      ChangeTariffPlanComponent,
      DeactivateCardComponent           
  ],
  providers: [
    
  ]
})
export class ActionsModule {}



