import { Component, AfterViewInit } from '@angular/core';
import { ScriptService } from './script.service';
declare var $: any;
declare var Layout: any;
import { Router, ActivatedRoute } from '@angular/router';
import { DataService } from './data.service';
import { AuthenticationService } from './services/authentication.service';

@Component({
    selector: 'menu-app',
    templateUrl: '/app.menu.component.html'
})

export class MenuComponent {
    pageTitle: string = 'Dealer Commissioning';
   
    scriptService: ScriptService = new ScriptService();
    
    constructor(private dataService: DataService, private authenticationService: AuthenticationService, private router: Router) {    
    }

    selectedItem: string = this.dataService.selectedMenuItem$;

    selectMenu(menuItem: string): void {
                
        this.dataService.selectedMenuItem$ = menuItem;



    }

    isAdminUser()
    {
        return this.authenticationService.isAdminUser()
    }

    ngAfterViewInit() {
        //console.log($(window));
       // $(window).Layout.init();
        (<any>Layout.init());
        
    }

ngOnInit(): void {
     
    }     
}



