import { Component, OnInit, OnDestroy, Input, Output, Injectable } from '@angular/core';
import { Http, Headers, Response, RequestOptions } from '@angular/http';
import { environment } from '../environments/environment';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/throw';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import { ICustomerResults, IInvoiceResults} from './shared/interfaces';
import { SearchCriteria } from './shared/search.criteria';
import { Customer } from './customers/customer';
import { CustomerRelation } from './customers/customerRelation';
import { UserActivity } from './home/user.activity';
import { Invoice } from './billing/invoice';
import {TreeModule, TreeNode} from 'primeng/primeng';
import { User } from './users/user';

declare var $: any;

@Injectable()
export class DataService {

    //public serverUrl: string = "http://82.146.27.149:5555";

     //serverUrl: string = "http://192.168.1.10:5556";
    public serverUrl = environment.url;


    public customersList$: Customer[] = [];
    public searchCriteria$: SearchCriteria = new SearchCriteria();
    public totalRecords$: number = 0;

    private usersList$: User[] = [];
    public invoicesList$: Invoice[] = [];
    private largeAccountsList$: TreeNode[][] = [];
    private selectedCustomer$: Customer = new Customer();
    private selectedUser$: User = new User();
    public selectedUserId$: number = 0;
    public selectedMenuItem$: string = "welcome";
    public previousPageRoute$: string = "";
    public currentPageRoute$: string = "";

    constructor(private http: Http) {

    }
  
    getCustomers(searchCriteria: SearchCriteria): Observable<Customer[]> {

        var url: string = this.serverUrl + '/api/customers/page';
        
        return this.http.post(url, searchCriteria)
            .map((res: Response) => {
                
                let customers = res.json().customers;
                //this.calculateCustomersOrderTotal(customers);
                return customers;
            })
            .catch(this.handleError);
    }

    getInvoices(searchCriteria: SearchCriteria): Observable<Invoice[]> {

        var url: string = this.serverUrl + '/api/invoices/list';       
        return this.http.post(url, searchCriteria)
            .map((res: Response) => {
               
                let invoices = res.json().invoices;
                //this.calculateCustomersOrderTotal(customers);
                return invoices;
            })
            .catch(this.handleError);
    }

    getInvoicesPage(searchCriteria: SearchCriteria): Observable<IInvoiceResults> {
        var url: string = this.serverUrl + '/api/invoices/list';
        
        return this.http.post(url, searchCriteria)
            .map((res: Response) => {
                //const totalRecords = +res.headers.get('x-inlinecount');
                let invoices = res.json().invoices;
                
                var totalRows = 0;
                if (invoices.length > 0) {
                    totalRows = invoices[0].totalRows;
                }

                var results: IInvoiceResults = {
                    results: invoices,
                    totalRecords: totalRows
                };

                return results;
            })
            .catch(this.handleError);
    }

    getCustomersPage(searchCriteria: SearchCriteria): Observable<ICustomerResults> {
        var url: string = this.serverUrl + '/api/customers/page';
        
        return this.http.post(url, searchCriteria)
            .map((res: Response) => {
                //const totalRecords = +res.headers.get('x-inlinecount');
                let customers = res.json().customers;

                var totalRows = 0;
                if (customers.length > 0)
                {
                    totalRows = customers[0].totalRows;
                }

                var results: ICustomerResults = {
                    results: customers,
                    totalRecords: totalRows
                };
               
                return results;
            })
            .catch(this.handleError);
    }


    getUsers(searchtext: string): Observable<User[]> {

        var url: string = this.serverUrl + '/api/portalusers/search/' + searchtext;

        return this.http.get(url)
            .map((res: Response) => {
                
                let portalusers = res.json().portalUsers;
                //this.calculateCustomersOrderTotal(customers);
                return portalusers;
            })
            .catch(this.handleError);
    }

    getCustomer(customerId : number): Observable<Customer> {

        
        var url: string = this.serverUrl + '/api/customers/' + customerId;
        
        return this.http.get(url)
            .map((res: Response) => {
                
                let customer = res.json();
                
                return customer;
            })
            .catch(this.handleError);
    }

    getCustomersStatistics(createdByUserId: number): Observable<number> {
       
        var url: string = this.serverUrl + '/api/customers/count/' + createdByUserId;

        return this.http.get(url)
            .map((res: Response) => {

                let statistics = res.json();

                return statistics;
            })
            .catch(this.handleError);
    }

    getPortalUser(userId: number): Observable<User> {

        var url: string = this.serverUrl + '/api/portalusers/' + userId;

        return this.http.get(url)
            .map((res: Response) => {

                let user = res.json();

                return user;
            })
            .catch(this.handleError);
    }

    getUserActivity(userId: number, count: number, fromCreateDate: Date, toCreateDate: Date, activityType: string): Observable<UserActivity[]> {

        var criteria: SearchCriteria = new SearchCriteria();
        criteria.userId = userId;
        criteria.pageSize = count;
        if(fromCreateDate != undefined) 
          criteria.fromInvoiceDate = fromCreateDate;
        criteria.activityType = activityType;
        if (toCreateDate != undefined)
          criteria.toInvoiceDate = toCreateDate;

        var url: string = this.serverUrl + '/api/useractivity/list';
        
        return this.http.post(url, criteria)
            .map((res: Response) => {
                
                let useractivities = res.json();

                return useractivities;
            })
            .catch(this.handleError);
    }

     getCustomerTree(customerId: number): Observable<Customer[]> {

         var url: string = this.serverUrl + '/api/customers/tree/' + customerId;

            return this.http.get(url)
                .map((res: Response) => {
                console.log(res.json());
                let customers = res.json();

                return customers;
            })
            .catch(this.handleError);
    }

    addUser(user: User) {
        this.usersList$.push(user);
    }

    cacheCustomerSearch(searchCriteria: SearchCriteria, customersList: Customer[], totalRecords: number)
    {
        
        this.searchCriteria$ = searchCriteria;
        this.customersList$ = customersList;
        this.totalRecords$ = totalRecords;
    }
     cacheInvoiceSearch(searchCriteria: SearchCriteria, invoicesList: Invoice[], totalRecords: number)
     {
         
        this.searchCriteria$ = searchCriteria;
        this.invoicesList$ = invoicesList;
        this.totalRecords$ = totalRecords;
    }

    saveCustomer(customer: Customer): Observable<Customer> {
        
        var url: string = this.serverUrl + '/api/customers/save';

        return this.http.post(url, customer)
            .map((res: Response) => {
                
                const data = res.json();                
                return data.customer;                
            })
            .catch(this.handleError);
    }

    saveCustomerRelation(changedCustomers: Customer[], deletedCustomers: Customer[]): Observable<Customer> {
        
        var url: string = this.serverUrl + '/api/customers/saverelation';

        var customerRelation: CustomerRelation = new CustomerRelation();
        customerRelation.changedCustomers = changedCustomers;
        customerRelation.deletedCustomers = deletedCustomers;

        return this.http.post(url, customerRelation)
            .map((res: Response) => {
                
                const data = res.json();                
                return data.customer;                
            })
            .catch(this.handleError);
    }

    saveUser(user: User): Observable<User> {

        var url: string = this.serverUrl + '/api/portalusers/save';
       
        return this.http.post(url, user)
            .map((res: Response) => {

                const data = res.json();
                return data.portalusers;
            })
            .catch(this.handleError);
    }

    saveUserActivity(userActivity: UserActivity): Observable<UserActivity> {

        var url: string = this.serverUrl + '/api/useractivity/save';

        return this.http.post(url, userActivity)
            .map((res: Response) => {

                const data = res.json();
                
                return data.useractivities;
            })
            .catch(this.handleError);
    }

    getLargeAccounts() {
        return this.largeAccountsList$;
    }

    getLargeAccountByCustomer() {

        var customerAccount: TreeNode[] = [];
        
        //if (this.selectedCustomerId$ > 0) {

        //    for (let largeAccount of this.largeAccountsList$) {
        //        for (let customer of largeAccount) {

        //            if (customer.data == this.selectedCustomerId$)
        //                customerAccount = largeAccount;

        //            for (let child of customer.children) {

        //                if (child.data == this.selectedCustomerId$)
        //                    customerAccount = largeAccount;

        //                for (let subChild of child.children) {
        //                    if (subChild.data == this.selectedCustomerId$)
        //                        customerAccount = largeAccount;
        //                }
        //            }
        //        }
        //    }

        //}

        return customerAccount;
    }

    setSelectedCustomer(customer: Customer) {
        this.selectedCustomer$ = customer;
    }

    getSelectedCustomer() {
        return this.selectedCustomer$;
    }

    setSelectedUser(user: User) {
        this.selectedUser$ = user;
    }

    getSelectedUser() {
        return this.selectedUser$;
    }

    addLargeAccount(currentLargeAccount: TreeNode[]) {
        this.largeAccountsList$.push(currentLargeAccount);
    }

    deleteLargeAccount(currentLargeAccount: TreeNode[]) {

        var currentIndex: number = 0;

        for (let largeAccount of this.largeAccountsList$) {
            var found: boolean = true;
            if (largeAccount.length == currentLargeAccount.length) {

                var index: number = 0;
                for (let customer of largeAccount) {

                    if (largeAccount[index].data != customer.data) {
                        found = false;
                    }
                    index++;
                }
            }
            else {
                found = false;
            }

            if (found) {
                this.largeAccountsList$.splice(currentIndex, 1);
            }
            currentIndex++;
        }

        
    }

    addInvoice(invoice: Invoice) {
        this.invoicesList$.push(invoice);
    }
    private handleError(error: any) {
       
        return Observable.throw(error || error.json().message);
    }

    deleteCustomer(id: number): Observable<boolean> {
        return this.http.delete(this.serverUrl + '/api/customers/' + id)
            .map((res: Response) => res.json().status)
            .catch(this.handleError);
    }

    deleteUser(id: number, createdByUserId: number): Observable<boolean> {
        return this.http.delete(this.serverUrl + '/api/portalusers/' + id + "/" + createdByUserId)
            .map((res: Response) => res.json().status)
            .catch(this.handleError);
    }
    changePassword(userId: number, password: string, oldPassword: string): Observable<boolean> {

        var url: string = this.serverUrl + '/api/portalusers/password/' + userId + "/" + password + "/" + oldPassword;
        
        //return this.http.post(this.serverUrl + '/api/portalusers/login/', JSON.stringify({ username: username, password: password }))
        return this.http.get(url)
            .map((res: Response) => {

                const data = res.json();
                return true;
            })
            .catch(this.handleError);
    }

    customerStatusText(status : string): string {
        if (status == "OnHold")
            return "On Hold";
        
        return status;
    }

    menuUnselect() {

        //$(".portalParentMenuItem").removeClass("active");
        $(".portalMenuItem").removeClass("active");
        
    }

}



