import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpModule } from '@angular/http';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';

import { UserComponent } from './user.component';
import { UserFilterPipe } from './user-filter.pipe';
import {PasswordModule} from 'primeng/primeng';
import { UserSearchComponent } from './user.search.component';
import { UserChangePasswordComponent } from './user.change.password.component';
import { AuthGuard } from '../guards/auth.guard';
import { AdminGuard } from '../guards/admin.guard';
import { SiteLayoutComponent } from '../layout/site-layout.component';

@NgModule({
  imports: [

      BrowserModule,
      HttpModule,
      CommonModule,
      FormsModule,
      PasswordModule,
      RouterModule.forRoot([
          {
              path: 'users',
              component: SiteLayoutComponent,
              children: [
                  { path: 'user', component: UserComponent, canActivate: [AdminGuard] },
                  { path: 'user-search', component: UserSearchComponent, canActivate: [AdminGuard] },
                  { path: 'user/:id', component: UserComponent, canActivate: [AdminGuard] },
                  { path: 'user-change-password', component: UserChangePasswordComponent, canActivate: [AuthGuard] }
              ]
          }         
    ])
  ],
  declarations: [
      UserComponent,
      UserSearchComponent,
      UserChangePasswordComponent,      
      UserFilterPipe     
  ],
  providers: [
    
  ]
})
export class UsersModule {}



