import { Component, AfterViewInit } from '@angular/core';
import { ScriptService } from './script.service';
import { AuthenticationService } from './services/authentication.service';

declare var $: any;
declare var Layout: any;

@Component({
    selector: 'top-menu',
    templateUrl: '/top.menu.component.html'
})

export class TopMenuComponent {
    pageTitle: string = 'Dealer Commissioning';
   
    scriptService: ScriptService = new ScriptService();
    userName: string = "";

    constructor(private authenticationService: AuthenticationService) {    
    }
    
    selectedItem: string = "welcome";

    selectMenu(menuItem: string): void {

        this.selectedItem = menuItem;

    }

    logout(): void {

        

    }
  
    ngOnInit(): void {
        this.userName = this.authenticationService.loggedUser$.firstName + " " + this.authenticationService.loggedUser$.lastName;
    }     
}



