import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpModule } from '@angular/http';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';

import { PrePaidComponent } from './pre.paid.component';
import { PostPaidComponent } from './post.paid.component';

import { AuthGuard } from '../guards/auth.guard';
import { SiteLayoutComponent } from '../layout/site-layout.component';

@NgModule({
  imports: [

      BrowserModule,
      HttpModule,
      CommonModule,
      FormsModule,
     
      RouterModule.forRoot([

          {
              path: 'subscribers',
              component: SiteLayoutComponent,
              children: [
                  { path: 'pre-paid', component: PrePaidComponent, canActivate: [AuthGuard] },
                  { path: 'post-paid', component: PostPaidComponent, canActivate: [AuthGuard] }
              ]
          }        
      
                  
    ])
  ],
  declarations: [
      PrePaidComponent,
      PostPaidComponent           
  ],
  providers: [
    
  ]
})
export class SubscribersModule {}



